
body {
  font-family: "Microsoft YaHei UI Light";
}
.head {
  width: 100%;
  height: 150px;
  border-bottom: 1px solid #CCCCCC;
  box-sizing: border-box;
  padding-top: 30px;
  text-align: center;
}
.head > h1 {
  font-size: 100px;
  color: #2bb48a;
  font-weight: 400;
  margin: 0;
  line-height: 100px;
  vertical-align: baseline;
}
.acticon_top {
  text-align: center;
  width: 600px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 20px 10px;
}
.acticon_top .acticon__title {
  font-size: 18px;
  font-weight: 400;
  color: #555555;
  margin-top: 2px;
}
.acticon_top .actico__desc {
  font-size: 13px;
  font-weight: 400;
  color: #2bb48a;
}
.acticon_top .acticon__anthor {
  margin: 10px 0;
  font-size: 16px;
  color: #2bb48a;
  font-weight: normal;
}
.acticon_top .acticon__anthor a:hover {
  color: #87daff;
}
.acticon_top .acticon__anthor .guoch {
  color: #2f9842;
  text-decoration: none;
}
.acticon_top .contribute {
  cursor: pointer;
  display: inline-block;
  line-height: 50px;
  width: 120px;
  height: 50px;
  background-color: #fff;
  border: 1px solid #2bb48a;
  font-size: 14px;
  color: #2bb48a;
  margin: 20px 0 0 0;
  text-decoration: none;
}
.acticon_top .contribute:hover {
  outline: 1px solid #87daff;
}
@media screen and (max-width: 740px) {
body {
    margin: 0 10px;
}
.head {
    height: 110px;
    padding-top: 50px;
}
.head > h1 {
    font-size: 40px;
    color: #2bb48a;
    font-weight: 400;
    margin: 0;
    line-height: 50px;
    vertical-align: baseline;
}
.acticon_top {
    width: 100%;
}
}



